import React, { Component } from 'react'
import ImagePreloader from '../ImagePreloader/ImagePreloader'

export default class Project extends Component {

    render() {
        return (
            <li>
                <img src={this.props.url} alt="" data-uk-cover/>
                <div className="uk-position-center uk-border-rounded uk-position-small uk-text-center uk-light overlay-shadow" >
                    <h2 className="uk-margin-remove">{this.props.name}</h2>
                    <p className="uk-margin-remove uk-text-emphasis">{this.props.description}</p>
                    {this.props.urlSite ? <a href={this.props.urlSite}>Ir al sitio...</a> : <p className="uk-margin-remove">Este proyecto es privado.</p>}
                </div>                
            </li>
        )
    }
}
